export default {
	name:"zhuanti",
	data(){
		return {
			banner:[],
			album:[]
		}
	},
	methods:{
		zhuanti(){
			uni.request({
				url:"/image/v1/wallpaper/album",
				data:{
					limit:10,
					order:"new",
					skip:0
				},
				success: (result) => {
					console.log(result.data.res.album);
					this.banner = result.data.res.banner;
					this.album = result.data.res.album;
					
				}
			})
		}
	},
	mounted(){
		this.zhuanti();
		uni.setNavigationBarTitle({
		    title: '专题'
		});			
	}
}
